import { Col, Container, Row } from 'react-bootstrap';

import './accommodations-body.css';

const rooms = [
  {
    name: 'Double Standard',
    img: '/images/hotel/accommodation/accommodations-2.jpg',
    details: [
      'habitaciones de 30 m²',
      'Una cama king-size (200x200 cm) o dos camas dobles pequeñas (125x200 cm)',
      'Televisión satelital',
      'Aire acondicionado',
      'Un minibar y dispensador de bebidas',
      'Un balcón o terraza',
    ],
  },
  {
    name: 'Double Room with sea view',
    img: '/images/hotel/accommodation/accommodations-3.jpg',
    details: [
      'Habitaciones Dobles Vista Mar',
      'habitaciones de 30 m²',
      'Una cama king-size (200x200 cm) o dos camas dobles pequeñas (125x200 cm)',
      'Televisión satelital',
      'Aire acondicionado',
      'Un minibar y dispensador de bebidas',
      'Un balcón o terraza',
    ],
  },
];
function AccommodationsBody() {
  return (
    <div className="accommodations-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>Alojamientos</h1>
          </Col>
        </Row>
        {rooms.map((room) => {
          return (
            <Row className="justify-content-md-center room-detail-list mb-2">
              <Col sm={4} className="text-left p-1">
                <img className="room-img" src={room.img} alt=""></img>
              </Col>
              <Col sm={8} className="text-left p-2">
                <h4 className="mb-2">{room.name}</h4>
                <ul className="accommodations-list">
                  {room.details.map((detail, i) => {
                    return (
                      <li key={i}>
                        <i className="fa fa-check-circle"></i> {detail}
                      </li>
                    );
                  })}
                </ul>
              </Col>
            </Row>
          );
        })}
      </Container>
    </div>
  );
}

export default AccommodationsBody;
